"use strict";

var EE = require("events").EventEmitter;

function DataQueue() {
  var ee = new EE();
  var q = [];
  var ed;

  function unleak() {
    ee.removeAllListeners("err");
    ee.removeAllListeners("data");
  }

  return {
    append: function append(data) {
      if (ed) return ed;
      q.push(data);
      ee.emit("data");
    },
    prepend: function prepend(data) {
      //better only call this before the get queue starts
      if (ed) return ed;
      q.unshift(data);
    },
    error: function error(e) {
      ed = e;
      ee.emit("err", e);
    },
    get: function get(cb) {
      unleak();
      if (ed) return cb(ed);
      if (q.length) return cb(null, q.shift());
      ee.once("err", function (e) {
        unleak();
        cb(e);
      });
      ee.once("data", function () {
        unleak();
        return cb(null, q.shift());
      });
    },
    height: function height() {
      return q.length;
    }
  };
}

module.exports = DataQueue;